.cart-page {
  margin-top: 64px;
}

.cart-page p {
  font-weight: 300;
  font-size: 18px;
}

.cart-page h1 {
  padding: 15px !important;
  font-family: "roboto", sans-serif;
  font-weight: normal;
  background-color: rgba(0, 0, 255, 0.072) !important;
}
.cart-page h1 > p {
  font-size: 20px;
  margin-top: 10px;
}
.price {
  color: #009688;
}
.cart-page .card {
  padding: 5px !important;
  margin-bottom: 5px !important;
}
.cart-page .cart-remove-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.cart-page .cart-summary {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.increment_icon {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: aliceblue;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.number_input {
  width: 50px;
  border: 1px solid gray;
  background-color: aliceblue;
  border-radius: 5px;
}

.order_summery {
  border: 1px solid #dbc5c5;
  width: 100%;
  padding: 5px;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
}

.order_summery span {
  font-weight: 300px;
}
.order_summery div {
  border-bottom: 1px solid #dbc5c5;
  margin: 10px 0px;
}

.cart-img{
  width: 100%;
  height: 200px;
  border-radius: 10px;
}
.cart-wrapper_card{
  width: 100%;
  display: flex;
  padding: 10px;
  margin: 10px 0;
  border: 1px solid darkgray;
  gap: 10px;
}

@media (max-width: 770px) {

  .cart-wrapper_card{
    justify-content: space-around;

  }
  .cart-page p {
    margin-bottom: 0;
  }
  .cart-img{
    width: 100px;
    height: 100px;
  }
 
}

@media (max-width: 475px) {
  .cart-page p {
    font-size: 15px;
  }
}