.category_card {
  border: 1px solid #7a7bdd;
  border-radius: 50px;
  display: flex;
  align-items: center;
  font-weight: 600;
  font-size: 13px;
  padding: 5px 10px;
  max-width: 120px;
  width: 100%;
  cursor: pointer;
}

.catg-link {
  color: #7a7bdd;
  font-size: 12px;
}

.selected_category {
  background-color: #7a7bdd;
  cursor: pointer;
}

.selected_category .catg-link {
  color: white;
}

@media (max-width: 475px) {
  .category_card {
    font-size: 10px;
    padding: 2px 5px;
  }
}
