.filter_modal {
  padding: 15px;
}

.cancel-btn {
  background-color: rgb(85, 85, 85);
  width: 100px;
  height: 36px;
  border-radius: 18px;
  color: white;
}

.save_btn {
  width: 100px;
  height: 36px;
  border-radius: 18px;
  background-color: #1c89ff;
  border: solid 1px transparent;
  color: #fff;
  font-size: 18px;
  font-weight: 500;
  cursor: pointer;
}

.btn-wrapper {
  display: flex;
  justify-content: end;
  gap: 20px;
  margin-top: 20px;
}
