.product_card {
  max-width: 250px;
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 10px;
  border: 1px solid #f5e1e1;
  border-radius: 10px;
}

.product_img {
  border-radius: 10px;
  width: 100%;
  height: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: aliceblue;
  transition: opacity 0.3s, transform 0.3s;
}

.product_img img {
  max-width: 170px;
  width: 100%;
  height: 170px;
  padding: 10px;
  border-radius: 15px;
}

.product_img img:hover {
  opacity: 1;
  transform: scale(1.3);
}
.card-text {
  color: #899294;
}
.card-title,
span {
  font-weight: 600;
}
.addTOcart_btn {
  font-size: 12px;
  font-weight: bold;
  color: #fff;
  padding: 7px 10px;
  letter-spacing: 2px;
  background-color: #009688;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  box-shadow: 0 2px 10px rgb(0 0 0 / 50%);
}

.addTOcart_btn:active {
  background-color: brown;
  color: white;
}
.more-details {
  color: #7a7bdd;
  text-decoration: underline;
}

@media (max-width: 475px) {
  .product_card {
    max-width: 155px;
    gap: 5px;
  }

  .product_img img {
    max-width: 120px;
    height: 110px;
  }

  .product_img {
    width: 100%;
    height: 100%;
    padding: 5px 0px;
  }
  .card-body span {
    font-size: 12px;
  }
  .addTOcart_btn {
    padding: 2px 4px;
    font-size: 8px;
    width: 100%;
    height: 24px;
  }
  .more-details {
    font-size: 11px;
    text-align: end;
  }
  .card-text {
    font-size: 14px;
  }
}
