@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&family=Poppins:wght@300;400&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  overflow-x: hidden;
}

a {
  color: black;
  text-decoration: none;
}
/* font-family: 'Poppins', sans-serif; */
/* font-family: 'Playfair Display', serif; */

.main_layout {
  min-height: 80vh;
  margin: 80px 15px 40px 15px;
}

/* //navbar css */
.navbar {
  font-family: "Poppins", sans-serif;
  font-size: 17px;
  line-height: 26px;
  text-transform: uppercase;
  box-shadow: 0 8px 6px -6px gray;
  --webkit-box-shadow: 0 8px 6px -6px gray;
  border-bottom: solid gray !important;
}

.nav-link {
  font-weight: 300 !important;
}

.active {
  border-bottom: 2px solid black;
}

.navbar-brand {
  font-weight: 700;
  font-family: "roboto", sans-serif;
  letter-spacing: 3px;
}

.search-form {
  margin-right: 10px;
  margin-top: 4px;
}
.search-form input {
  border: none;
  border-radius: 0;
}
.search-form button {
  background-color: #000000;
  border-radius: 0;
  color: white;
}

/* =============================
==========footer============= */

.footer {
  color: white;
  padding: 15px;
  background: #000000; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #434343,
    #000000
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #434343, #000000);
}

.footer a {
  text-decoration: none;
  color: white;
  padding: 10px;
}

.footer a:hover {
  color: #ffefba;
  border-bottom: 1px solid #ffefba;
}
/* =================================
======== page not found css ======= */
.pnf {
  display: flex;
  min-height: 65vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.pnf-title {
  font-size: 100px;
  font-weight: 700;
}

.pnf-heading {
  font-weight: normal;
}

.pnf-btn {
  color: black;
  border: 1px solid black;
  text-decoration: none;
  padding: 10px;
  margin-top: 10px;
}
.pnf-btn:hover {
  background-color: black;
  color: white;
}
/* ========================================= */
/* =========contact us ========= */
.contactus {
  margin: 0;
  padding: 0;
  height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* ==================== */
.product-link {
  text-decoration: none !important;
  color: black !important;
}

.cat-btn {
  padding: 40px 0px;
  font-size: 24px;
  text-transform: uppercase;
}
.cat-btn:hover {
  background-color: #434343;
  color: white;
}
/* ================
====dashboard
=================== */

.dashboard {
  margin-top: 100px !important;
}

.dashboard-menu h4 {
  background-color: #434343 !important;
  color: white;
  padding: 20px 0px;
}

.dropdown-item.active {
  background-color: white !important;
  color: black !important;
  border: none;
}

.ant-badge-count {
  top: -6px !important;
  left: 15px;
}

/* carousel-images */
.carousel-images {
  margin: auto;
}

/* Styling for the custom card */
.card-custom {
  width: 200px;
  border: none;
  padding: 4px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s;
}

.card-custom:hover {
  transform: translateY(-5px);
}

.edit_cat {
  width: 90px;
  height: 32px;
  border-radius: 18px;
  background-color: #1c89ff;
  border: solid 1px transparent;
  color: #fff;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
}

.delete_cat {
  width: 90px;
  height: 32px;
  border-radius: 18px;
  background-color: #df770fda;
  border: solid 1px transparent;
  color: #fff;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
}

/* Styling for the card image */
.card-img-tops {
  width: 100%;
  /* object-fit: cover; */
  height: 150px; /* Adjust to desired height */
}

/* Styling for the card body */
.card-body-part {
  padding: 1.25rem; /* Adjust padding as needed */
}

.card-titles {
  font-size: 1.25rem;
  font-weight: bold;
}

.card-texts {
  color: #666;
}

/* search input  */
/* Add this CSS to your stylesheet */
.search-input-container {
  border-radius: 9px;
  padding: 5px;
}

.search-input {
  background: cyan;
  border-radius: 18px;
}

/* Responsive adjustments */
@media (max-width: 475px) {
  .card-custom {
    width: 150px;
  }

  .card-img-tops {
    height: 150px;
  }

  .card-texts,
  .card-titles {
    font-size: 13px;
  }
  .order_image {
    width: 200px;
  }
}

@media (max-width: 475px) {
  .main_layout {
    margin: 80px 10px 10px;
  }
}
