.product-details {
  margin: 20px;
  display: flex;
  justify-content: center;
  gap: 10px;
}

.left-container {
  flex: 40%;
}

.left-container img {
  width: 100%;
}

.right-container {
  flex: 60%;
  /* margin-left: 40px; */
  padding-left: 30px;
}

.right-container h1 {
  font-weight: 300;
  font-size: 44px;
  letter-spacing: -2px;
}

.right-container h2 {
  font-weight: 200;
  font-size: 20px;
  letter-spacing: 1px;
  color: rgb(27, 25, 25);
}

.right-container h2 span {
  color: #009688;
  font-weight: bold;
}

.right-container p {
  margin-top: 20px;
  font-size: 16px;
  font-weight: 300;
  color: #86939e;
}


.right-container .preview-images img {
  width: 15%;
  height: 30%;
  cursor: pointer;
  padding: 4px;
}

.right-container button {
  font-size: 16px;
  font-weight: bold;
  color: #fff;
  padding: 10px 20px;
  letter-spacing: 2px;
  background-color: #009688;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  box-shadow: 0 2px 10px rgb(0 0 0 / 50%);
}
/* 
.active {
  outline: 2px solid #009688;
  outline-offset: 4px;
  border-radius: 2px;
} */

@media (width <= 745px) {
  .product-details {
    flex-direction: column;
  }

  .right-container {
    padding-left: 0;
  }
  .right-container h1{
    font-size: 20px;
    letter-spacing: 0;
  }
}
